<script>
import http from "../../network/Base.js";
import login from "../../network/corporate/signin.js";

export default {
  name: "clogin",
  data() {
    return {
      loading: false,
      isLoggingIn: false,
      company: {
        email: "",
        password: ""
      }
    };
  },
  methods: {
    submit() {
      this.loading = true;
      login.adminSignIn(this, http(), this.company);
    },
    login() {
      this.loading = true;
      this.$store
        .dispatch("login", this.company)
        .then(res => {
          /* if (res.data.hasPolicy) this.$store.commit("has_policy", true);
          if (res.data.hasBilling) this.$store.commit("has_billing", true);
          if (!!res.data.hasPolicy && !!res.data.hasBilling) */
          this.$router.push("/");
          /* else this.$router.push("/account"); */
        })
        .catch(err => {
          this.loading = false;
          this.$snotify.error("Incorrect email or password!");
          console.log(err);
        });
    }
  },
  created() {
    // autofill
    if (this.$route.query["email"]) {
      this.company.email = this.$route.query["email"];
    }
  }
};
</script>

