<script>
import { mapState } from "vuex";
import http from "@/network/Base.js";

export default {
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      company: {
        sz_fname: this.data.sz_firstname,
        sz_lname: this.data.sz_lastname,
        sz_oname: this.data.sz_othername,
        sz_email: this.data.sz_email,
        sz_gender: this.data.sz_gender,
        sz_dob: this.data.sz_dob,
        sz_phoneno: this.data.sz_phoneno,
        md_dependent: this.data.dependents
      }
    };
  },
  methods: {
    addDepend() {
      this.company.md_dependent.push({
        name: "",
        relation: "",
        dob: "",
        no: ""
      });
    },
    removeDepend(index) {
      this.company.md_dependent.splice(index, 1);
    },
    gmSpeciallyStringifyArray(array) {
      if (Array.isArray(array)) {
        let t = JSON.stringify(array);
        return t.substring(1, t.length - 1);
      } else {
        console.log("ERR - stringifyArray: argument not an array");
        return null;
      }
    },
    //update function
    updateEmployee() {
      console.log(this.data);
      let payload = {
        adminid: this.iD,
        sz_fname: this.company.sz_fname,
        sz_lname: this.company.sz_lname,
        sz_oname: this.company.sz_oname,
        sz_email: this.company.sz_email,
        sz_gender: this.company.sz_gender,
        sz_dob: this.company.sz_dob,
        sz_phoneno: this.company.sz_phoneno,
        sz_companyname: this.companyName,
        sz_companyid: this.sz_companyid,
        dependent: this.gmSpeciallyStringifyArray(this.company.md_dependent),
        memberid: "125555555"
      };
      this.loading = true;
      console.log(payload);
      http()
        .post("/company/member/update", payload)
        .then(res => {
          this.$router.push({ path: "/employee" });
        })
        .catch(error => {
          console.log(error);
          this.$snotify.error("Error occured during update!");
        })
        .finally(() => {
          this.loading = false;
        });
      this.$store.commit("employee/setEmployeeDetails", this.company);
    },
    cancelUpdate() {
      this.$router.push("/employee");
    }
  },
  created() {
    if (Array.isArray(this.data.dependents)) {
      this.company.md_dependent = this.data.dependents;
    } else {
      this.company.md_dependent = [];
    }
    //this splits the username into fname, oname and lname
    let splitName = this.data.sz_username.split(" ");
    if (splitName.length == 2) {
      this.company.sz_fname = splitName[0];
      this.company.sz_lname = splitName[1];
    } else {
      this.company.sz_fname = splitName[0];
      this.company.sz_lname = splitName[2];
      this.company.sz_oname = splitName[1];
    }
  },
  computed: {
    ...mapState({
      sz_companyid: state => state.corporate.company_id,
      companyName: state => state.corporate.company_name,
      iD: state => state.corporate.id
    })
  }
};
</script>

