<template>
  <div class="container">
    <div class="row justify-content-center mt-7" style="margin-bottom: 72px; padding-bottom: 24px;">
      <div class="col formCard">
        <div>
          <CompanyInfo
            nextStep="companySignIn"
            v-on:setNextStep="setNextStep($event)"
            v-if="currentStep === 'companyInfo'"
          />
          <CompanySignin
            nextStep="checkMail"
            previousStep="companyInfo"
            v-on:setNextStep="setNextStep($event)"
            v-on:setPreviousStep="setPreviousStep($event)"
            v-if="currentStep === 'companySignIn'"
          />
          <CheckMail
            v-on:saveEmail="saveEmail($event)"
            previousStep="companySignIn"
            nextStep="accountCreated"
            v-on:setNextStep="setNextStep($event)"
            v-on:setPreviousStep="setPreviousStep($event)"
            v-if="currentStep === 'checkMail'"
          />
          <AccountCreated
            nextStep="paymentCard"
            v-on:setNextStep="setNextStep($event)"
            v-if="currentStep === 'accountCreated'"
          />
          <PaymentCard v-if="currentStep === 'paymentCard'" />
        </div>
      </div>
      <div style="padding-left: 35px;" class="col stepper d-none d-md-block">
        <div class="row">
          <div class="col p-0">
            <div class="progress1">
              <div
                class="progress-step"
                :class="{'active' :index === activeStep}"
                v-for="(icon, index) in formSteps"
                :key="'icon'+index"
              >
                <i :class="icon.icon"></i>
              </div>
            </div>
          </div>
          <div class="col p-0">
            <div class="progress1">
              <div
                class="title-step"
                :class="{'active' :index === activeStep}"
                v-for="(title, index) in formSteps"
                :key="'title' + index"
              >{{title.title}}</div>
            </div>
          </div>
        </div>

        <h3 style="padding-top: 50px;">Your program summary:</h3>
        <div v-for="companyService in companyServices" :key="companyService">
          <p class="mb-0">{{companyService}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import CompanyInfo from "../StepCards/CompanyInfo";
import CompanySignin from "../StepCards/CompanySignin";
import CheckMail from "../StepCards/CompanyHealthPolicy";
import AccountCreated from "../StepCards/AddEmployees";
import PaymentCard from "../StepCards/PaymentForm";
// import VerifyEmail from '../StepCards/VerifyEmail'
export default {
  components: {
    CompanyInfo,
    CompanySignin,
    CheckMail,
    AccountCreated,
    PaymentCard
    // VerifyEmail
  },
  props: {
    Step: {
      type: String,
      default: () => {
        return "";
      }
    }
  },
  data() {
    return {
      activeStep: 0,
      currentStep: "companyInfo",
      formSteps: [
        { title: "Personal Info", icon: "fas fa-user" },
        { title: "Account Setup", icon: "fas fa-sign-in-alt" },
        { title: "Setup Health Policy", icon: "fas fa-thumbs-up" },
        { title: "Add Employees", icon: "fas fa-check-circle" },
        { title: "Add payment method", icon: "fas fa-credit-card" }
      ]
    };
  },
  methods: {
    setCurrentStep: function(step) {
      this.currentStep = step;
      this.activeStep += 1;
    },
    setNextStep: function(intent) {
      // console.log(`current step is ${thsis.currentStep}`);
      this.setCurrentStep(intent);
    },
    setPreviousStep: function(intent) {
      // console.log(`current step is ${this.currentStep}`);
      this.setCurrentStep(intent);
    },
    setStep: function(intent) {
      this.setCurrentStep(intent);
    },
    showStep(intent) {
      this.$emit("setStep", intent);
    }
  },
  computed: {
    ...mapState({
      companyServices: state => state.corporate.services
    })
  },
  created() {
    /* if (this.$route.query["step"] == "accountCreated") {
      this.currentStep = "accountCreated";
      this.activeStep = 3;
    }
    if (!this.$store.state.hasPolicy) {
      this.currentStep = "checkMail";
      this.activeStep = 2;
    } else if (!this.$store.state.hasBilling) {
      this.currentStep = "paymentCard";
      this.activeStep = 0;
    } */
  }
};
</script>

