<template>
  <div class="card card2">
    <div style="padding: 20px;">
      <h1>Sign Up and verify email</h1>
      <p>Set your password to complete sign up process with Serenity Health for {{companyName}}.</p>
    </div>
    <div class="card-body">
      <form @submit.prevent="showNextStep(nextStep)">
        <div class="form-group">
          <label>Your email</label>
          <input type="email" :value="sz_email" class="form-control" placeholder disabled />
        </div>
        <div class="form-group">
          <label>Password</label>
          <input
            type="password"
            v-model="company.sz_password"
            class="form-control"
            placeholder="Enter password"
            required
          />
        </div>
        <div class="form-group">
          <label>Confirmed Password</label>
          <input
            type="password"
            v-model="company.sz_password2"
            class="form-control"
            placeholder="Enter password"
            required
          />
        </div>
        <span class="animated fadeIn d-flex justify-content-end py-3">
          <button type="submit" class="btn btn-primary" :disabled="loading">
            <i v-if="loading" class="fa fa-spin fa-spinner"></i>
            <template v-else>VERIFY EMAIL</template>
          </button>
        </span>
      </form>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import http from "@/network/Base.js";

export default {
  props: {
    nextStep: {
      type: String,
      default: () => {
        return "";
      }
    }
  },
  data: () => ({
    loading: false,
    company: {
      sz_password: "",
      sz_password2: ""
    }
  }),
  methods: {
    showNextStep(intent) {
      let payload = {
        sz_password: this.company.sz_password,
        sz_firstname: this.sz_fname,
        sz_othername: this.sz_oname,
        sz_lastname: this.sz_lname,
        sz_gender: this.sz_gender,
        sz_email: this.sz_email,
        sz_phoneno: this.sz_phoneno,
        sz_dob: this.sz_dob,
        sz_companyid: this.sz_companyid,
        sz_companyname: this.companyName
      };

      let companyPayload = {
        sz_companyname: this.$store.state.sz_companyname,
        sz_phoneno: this.$store.state.sz_phoneno,
        sz_website: this.$store.state.sz_website,
        sz_address: this.$store.state.sz_address
      };

      this.loading = true;

      if (this.company.sz_password === this.company.sz_password2) {
        /* http()
          .post("/company/admin/add", payload)
          .then(res => {
            this.$emit("setNextStep", intent);
          })
          .catch(error => {
            this.$snotify.error("Error occured during registeration!");
          })
          .finally(() => {
            this.loading = false;
          }); */
        // onboard company
        http()
          .post("/auth/corporate/signup", companyPayload)
          .then(res => {
            const sz_companyid = res.data.sz_companyid;
            this.$store.commit("corporate/setCompanyId", sz_companyid);
            payload.sz_companyid = sz_companyid;
            console.log(res.data.sz_companyid);
            http()
              .post("/auth/corporate/admin/signup", payload)
              .then(res => {
                this.$emit("setNextStep", intent);
                this.loading = false;
              })
              .catch(error => {
                this.$snotify.error("Error occured during registration!");
              })
              .finally(() => {
                this.loading = false;
              });
          })
          .catch(error => {
            this.$snotify.error("Error occured during registration!");
          });
      } else {
        this.$snotify.error("Password doesnot much");
        this.loading = false;
      }

      this.$store.commit("corporate/setCompanyDetails", this.company);
    }
  },
  computed: {
    ...mapState({
      companyName: state => state.corporate.company_name,
      sz_companyid: state => state.corporate.company_id,
      sz_fname: state => state.corporate.first_name,
      sz_lname: state => state.corporate.last_name,
      sz_oname: state => state.corporate.other_name,
      sz_gender: state => state.corporate.gender,
      sz_email: state => state.corporate.work_email,
      sz_phoneno: state => state.corporate.phone_number,
      sz_dob: state => state.corporate.dob
    })
  }
};
</script>

<style scoped>
.card2 {
  width: 400px;
}
label {
  font-weight: bold;
}
</style>
