          bp: "37"
        }
      ]
    };
  },
  methods: {
    removeComplaint() {
      this.search = "";
    },

    toggle(i) {
      this.drop = !this.drop;
      this.selectedIndex = i;
      const index = this.opened.indexOf(i);
      if (index > -1) {
        this.opened.splice(index, 1);
      } else {
        this.opened.push(i);
      }
    },

    nameSearch(i) {
      this.search = this.tableData[i].name;
    }
  },
  computed: {
    ...mapState({
      sz_companyid: state => state.corporate.company_id
    }),
    tableData() {
      return this.tabdata
        .map(el => ({
          name: el["name"],
          date: el["date"],
          vid: el["vid"],
          location: el["location"],
          pid: el["vid"],
          bp: el["bp"]
        }))
        .filter(el => {
          let search = this.search.toLowerCase();
          if (
            el.name
              .toString()
              .toLowerCase()
              .search(search) > -1 ||
            el.date
              .toString()
              .toLowerCase()
              .search(search) > -1
          )
            return true;
          else return false;
        });
    }
  },
  created() {
    this.$Progress.start();
    this.loading = true;
    this.$store
      .dispatch("fetchActivities", this.company)
      .then(res => {
        const params_arr = [];
        res.data.forEach(el => {
          params_arr.push(el);
        });
        this.tabdata = this.details;
        this.loading = false;
        this.$snotify.info(
          `${this.tabdata.length} employee activities were found`
        );
      })
      .catch(() => {
        this.tabdata = this.details;
        this.loading = false;
        this.$snotify.info(
          `${this.tabdata.length} employee activities were found`
        );
      });
  }
};
</script>

