<template>
    <div class="px-5">
        <div class="row container-fluid mt-5">
            <div class="col-auto mr-auto"><h3>User Administration <span class="badge badge-primary"  style="font-size: 15px">{{tabdata.length}}</span></h3></div>
            <div class="col-auto">
                <router-link :to="{name: 'addAdmin'}">
                <button type="button" class="btn btn-primary btn-rounded" style="border-radius:20px"> <i class="fa fa-user-plus p-1"></i> Add User</button>
                </router-link>
    </div>
        </div>
         <div class="row mt-5 mx-5">
            <div class="col-md-6 col-sm-12 offset-md-3" style="padding-bottom:20px">
                <div class="input-group" >
                    <input type="text" class="form-control" id="searchInput" v-model="search" style="border-radius: 25px" placeholder=" Search...">
                    <div class="input-group-prepend">
                    </div>
                </div>
            </div>
        </div>
         <div class="text-center py-2" v-if="search">
                    <span  class="badge badge-pill badge-primary">
                    {{ search }}
                    <span class="remove-search-btn px-2" @click="removeComplaint(i)">&times;</span> 
                 </span>
                </div>
        <div class="mt-3">
            <div class="table">  
               <table class="table table-striped table-bordered">
                    <thead >
                        <tr>
                        <th scope="col" v-for="(header,index) in headers" :key="index">{{header.text}}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr  v-for="(detail, i) in tableData" :key="i">
                            <td class="py-2">{{detail.id}}</td>
                            <td class="py-2">{{detail.name}}</td>
                            <td class="py-2">{{detail.number}}</td>
                            <td class="py-2">{{detail.email}}</td>
                            <td class="py-2">
                            <!-- <base-dropdown tag="li" position='left'>
                               
                                <a class="dropdown-item"><i class="fa fa-user-edit"></i>Edit</a>
                                <a class="dropdown-item"  @click="deleteEmployee(selectedIndex)"><i class="fa fa-user-minus"></i>Delete</a>
                            </base-dropdown> -->
                             <i v-if="detail.id == iD" class="fas fa-eye" @click="viewProfile" style="font-size: 15px; color: blue"></i>
                             <i v-else class="fas fa-trash" @click="deleteAdmin(i)" style="font-size: 15px; color: red"></i>
                            </td>
                        </tr>
                    </tbody>
                    </table> 
            </div> 
             <div class="text-center" v-if="loading">
                <i  style="font-size:80px" class="fa fa-spin fa-spinner"></i>
            </div> 
            <p v-if="tableData.length === 0 && !loading" class="text-muted text-center display-3">
                Nothing to show
              </p>
        </div>
    </div>
</template>

<script>
import http from '@/network/Base.js' 
import {mapState} from 'vuex'
// import EmployeeService from '@/network/corporate/employee.js' 
export default {
    data() {
        return {
            loading: false,
            search:"",
            selectedIndex: 0,
            tabdata: [],
            headers:[
                {text:'ID', value: 'id',align: 'center',},
            { text: 'NAME', value: 'name',
                sortable: true, },
            { text: 'NUMBER', value: 'number' },
            { text: 'EMAIL', value: 'email' },
            {text:'ACTIONS', value: 'action'}
        ],
            // details:[
            //     {name:'James Garvin Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'Martha Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'Paul Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'James Carlos',number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'Peter Coch',number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'Hey There', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            //     {name:'Drink Water', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
            // ]
        }
    },
    methods:{
        removeComplaint(i) {
            this.search = ''
        },
        openNav(i) {
            document.getElementById("mySidenav").style.width = "500px";
            this.selectedIndex = i
        },

        closeNav() {
            document.getElementById("mySidenav").style.width = "0";
        },
        viewProfile() {
            this.$router.push('/profile')
        },
        deleteAdmin(i){
            let delDetail = {
                'email': this.tabdata[i].sz_email,
                'memberid': this.tabdata[i].sz_userid,
                }
            http().post("/company/members/deleteadmin", delDetail)
           .then(({data}) => {
               this.tabdata.splice(i, 1)
               this.$snotify.success('Successfully deleted') 
          })
          .catch(error => {
            console.log(error)
            this.$snotify.error('Error occured during registeration!') 
          })
          .finally(() => {
            this.loading = false
          })
        }
    },
    
    computed: {
          ...mapState({
            sz_companyid: (state) => state.corporate.company_id,
            iD: (state) => state.corporate.id,
       }),

        tableData() {
        return this.tabdata.map(el => ({
          name: el['sz_username'],
          number: el['sz_phoneno'],
          email: el['sz_email'],
          id: el['sz_userid']
        }))
        .filter((el) => {
          let search = this.search.toLowerCase()
          if(el.name.toString().toLowerCase().search(search) > -1 ) 
            return true
          else return false
        })
        }
    },
    created(){
        this.$Progress.start()
        this.loading = true
        http().post("/company/members/getallAdmin", { id: this.sz_companyid})
           .then(({data}) => {
               const params_arr = []
               data.forEach(el => {
                   params_arr.push(JSON.parse(el))
                   
               })
            //    console.log(params_arr)
               this.tabdata = [...params_arr]
            //    this.$store.commit('employee/setEmployeeDetails', this.tabdata)
               console.log(this.tabdata)
            })
            .catch(err => {
                this.$snotify.error('Could not get the list of Admins')
                this.$Progress.finish()
            })
            .finally(() => {
                this.loading = false
            })



    },
}
</script>

<style scoped>
.remove-search-btn{
    cursor: pointer;
}
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: rgb(223, 235, 250);
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}


.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

</style>
