// import net from '../Base'

export default {
    adminSignIn(e, http, payload) {
        e.$Progress.start()
        e.isSignedIn = true
        http.post('company/signin', {
            // net().post('user/signin', {
            sz_email: payload.email,
            sz_password: payload.password
        }).then((data) => {
            e.$Progress.finish()
            e.company = {}

            if (data.status === 400) {
                // e.$swal('Message', 'Incorrect email & password!!', 'error')
                console.log(data.status);
                return e.$snotify.error('Incorrect email or password!')
            }

            let userobject = null;
            userobject = data.data;

            if (userobject) {
                e.$store.commit('corporate/setLoggedIn', true)
                e.$store.commit('corporate/setCompanyDetails', userobject)

                e.$store.commit('corporate/setType', 'administrator')

                if (userobject.sz_compadmin) {
                    e.$store.commit('corporate/setCompanyId', userobject.sz_companyid)
                    e.$router.push('/')
                }
            } else {
                // e.$swal('Login Error', "Incorrect email & password!", 'error')
                e.$snotify.error('Incorrect email or password!')
            }
        }).catch(error => {
            // console.log(error);
            e.$Progress.finish()
            if (error.response) {
                e.$Progress.finish()
                if (error.response.status === 500) {
                    //    return e.$swal('System Error', 'Error occurred during authentication processing!', 'error')
                    return e.$snotify.error('An error occured whiles authenticating')
                } else if (error.response.status === 200) {
                    // e.$swal('Login Error', "Incorrect email & password!", 'error')
                    return e.$snotify.error('Incorrect email or password!')
                }
                // return e.$swal('Error', 'Incorrect email & password!!', 'error')
                return e.$snotify.error('Incorrect email or password!')
            }
            console.log(error)
            // return e.$swal('Error', 'Please check you internet connection', 'error')
            return e.$snotify.error('Could not sign you in. Check your internet connection!')
        }).finally(() => {
            e.isSignedIn = false
            e.loading = false
        })
    },
}