     methods:{
        
         addAdmin(){
            this.loading = true
            let payload = {
            'sz_password': this.company.sz_password,
            // 'sz_isadmin': 'True',
            'sz_fname': this.company.sz_fname,
            'sz_oname': this.company.sz_oname,
