            'sz_dob': this.company.sz_dob,
            'sz_companyid': this.sz_companyid,
            'sz_companyname': this.companyName
      }
                
        http().post('/company/admin/add', payload)
            .then((res) => {
                console.log('Response is successful')
                this.$router.push('/settings/admin')
            })
            .catch(error => {
                console.log(error)
                this.$snotify.error('Error occured during registeration!') 
            })
            .finally(() => {
                this.loading = false
            })
            
        }
    },
     computed: {
       ...mapState({
