<script>
// import http from '@/network/Base.js'
// import { CoolSelect } from "vue-cool-select";
// import Countries from "@/assets/countries.json";
import { mapState } from "vuex";

export default {
  components: {
    // CoolSelect,
  },
  props: {
    nextStep: {
      type: String,
      default: () => {
        return "";
      }
    }
  },
  data: () => ({
    loading: false,
    // selectedCountry: "",
    // items: Countries.data,
    company: {
      sz_fname: "",
      sz_lname: "",
      sz_oname: "",
      sz_gender: "",
      sz_email: "",
      sz_phoneno: "",
      sz_dob: "",
      sz_companyid: ""
    }
  }),
  methods: {
    showNextStep(intent) {
      this.company.sz_companyid = this.companyId;
      this.loading = true;
      this.$store.commit("corporate/setCompanyDetails", this.company);
      this.$emit("setNextStep", intent);
    }
  },
  computed: {
    ...mapState({
      companyName: state => state.corporate.company_name,
      companyId: state => state.corporate.company_id
    })
  }
};
</script>

