<script>
import btable from "../Tables/BillingTable";
import { SweetModal, SweetModalTab } from "sweet-modal-vue";
export default {
  data: () => ({
    loading: false,
    showPasswordMismatchNotification: false,
    password: "",
    confirmPassword: ""
  }),
  components: {
    btable,
    SweetModal,
    SweetModalTab
  },
  methods: {
    showForm() {
      if (!this.loading) this.$refs.modal.open("tab1");
    },
    submit() {
      if (this.password != this.confirmPassword) {
        this.showPasswordMismatchNotification = true;
        return;
      }
      this.$refs.modal.close();
      this.loading = true;
    }
  },
  watch: {
    password: function() {
      this.showPasswordMismatchNotification = false;
    },
    confirmPassword: function() {
      this.showPasswordMismatchNotification = false;
    }
  }
};
</script>
