            </div>
            <span class="animated fadeIn d-flex justify-content-end py-3">
              <button
                type="submit"
                class="btn btn-primary"
                :disabled="loading"
                @click="updateProfile"
              >
                <i v-if="loading" class="fa fa-spin fa-spinner"></i>
                <template v-else>Update</template>
              </button>
            </span>
