<script>
import http from "@/network/Base.js";
import { mapState } from "vuex";

export default {
  data() {
    return {
      loading: false,
      imageData: null,
      company: {
        sz_fname: "",
        sz_lname: "",
        sz_oname: "",
        sz_email: "",
        sz_gender: "",
        sz_dob: "",
        sz_phoneno: ""
      }
    };
  },
  methods: {
    chooseImage() {
      this.$refs.fileInput.click();
    },

    onSelectFile() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0]) {
        const reader = new FileReader();
        reader.onload = e => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      }
    },
    updateProfile() {
      let payload = {
        sz_fname: this.company.sz_fname,
        sz_lname: this.company.sz_lname,
        sz_oname: this.company.sz_oname,
        sz_email: this.company.sz_email,
        sz_gender: this.company.sz_gender,
        sz_dob: this.company.sz_dob,
        sz_phoneno: this.company.sz_phoneno,
        sz_companyname: this.company.companyName,
        sz_companyid: this.sz_companyid,
        sz_password: this.company.password
      };
      this.loading = true;

      http()
        .post("/company/admin/update", payload)
        .then(res => {
          this.$router.push({ path: "/" });
        })
        .catch(error => {
          console.log(error);
          this.$snotify.error("Error occured during registeration!");
        })
        .finally(() => {
          this.loading = false;
        });
      this.$store.commit("employee/setEmployeeDetails", this.company);
    }
  },
  computed: {
    ...mapState({
      companyName: state => state.corporate.company_name,
      companyId: state => state.corporate.company_id,
      Id: state => state.corporate.id,
      username: state => state.corporate.username,
      email: state => state.corporate.work_email,
      Cphoneno: state => state.corporate.company_number,
      site: state => state.corporate.company_site,
      address: state => state.corporate.company_address,
      phoneno: state => state.corporate.phone_number,
      address: state => state.corporate.company_address
    })
  }
};
</script>

