<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      loading: false,
      imageData: null
    };
  },
  methods: {
    chooseImage() {
      this.$refs.fileInput.click();
    },

    onSelectFile() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0]) {
        const reader = new FileReader();
        reader.onload = e => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      }
    }
  },
  computed: {
    ...mapState({
      companyName: state => state.corporate.company_name,
      username: state => state.corporate.username,
      companyId: state => state.corporate.company_id,
      email: state => state.corporate.work_email,
      Cphoneno: state => state.corporate.company_number,
      site: state => state.corporate.company_site,
      address: state => state.corporate.company_address,
      phoneno: state => state.corporate.phone_number,
      address: state => state.corporate.company_address
    })
  }
};
</script>

