<script>
import http from "@/network/Base.js";
import { mapState } from "vuex";
import { SweetModal, SweetModalTab } from "sweet-modal-vue";
// import EmployeeService from '@/network/corporate/employee.js'
export default {
  components: {
    SweetModal,
    SweetModalTab
  },
  data() {
    return {
      loading: false,
      search: "",
      selectedIndex: 0,
      tabdata: [],
      depend: [],
      headers: [
        { text: "", value: "avatar", align: "center" },
        { text: "NAME", value: "name", sortable: true },
        { text: "NUMBER", value: "number" },
        { text: "EMAIL", value: "email" },
        { text: "DEPENDENT", value: "dependent" },
        { text: "Policy", value: "policy" },
        { text: "", value: "avatar" }
      ]
      // details:[
      //     {name:'James Garvin Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'Martha Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'Paul Bond', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'James Carlos',number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'Peter Coch',number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'Hey There', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      //     {name:'Drink Water', number:'Developer', email: 'james@mercuri.com', dependant:'Specialist'},
      // ]
    };
  },
  methods: {
    removeComplaint(i) {
      this.search = "";
    },
    openNav(i) {
      document.getElementById("mySidenav").style.width = "500px";
      this.selectedIndex = i;
      console.log(this.tabdata[i].dependents);
    },

    closeNav() {
      document.getElementById("mySidenav").style.width = "0";
    },
    deleteEmployee(i) {
      let delDetails = {
        memberid: this.tabdata[i].sz_userid,
        email: this.tabdata[i].sz_email
      };
      http()
        .post("/company/members/deleteMembers", delDetails)
        .then(({ data }) => {
          this.tabdata.splice(i, 1);
          this.$snotify.success("Successfully deleted");
          document.getElementById("mySidenav").style.width = "0";
        })
        .catch(error => {
          this.$snotify.error("Error occured during registeration!");
        })
        .finally(() => {
          this.loading = false;
        });
    },
    editEmployee(i) {
      this.$router.push({ name: "edit", params: { data: this.tabdata[i] } });
    }
  },

  computed: {
    ...mapState({
      sz_companyid: state => state.corporate.company_id
    }),

    tableData() {
      return this.tabdata
        .map(el => {
          const ret = {
            name: `${el["sz_firstname"]} ${el["sz_lastname"]}`,
            number: el["sz_phoneno"],
            email: el["sz_email"]
            // depend: el['md_dependent']
          };
          if (Array.isArray(el["dependents"])) {
            ret.depend = el["dependents"];
          } else {
            ret.depend = [];
          }
          return ret;
        })
        .filter(el => {
          let search = this.search.toLowerCase();
          if (
            el.name
              .toString()
              .toLowerCase()
              .search(search) > -1
          )
            return true;
          else return false;
        });
    }
  },
  created() {
    this.$Progress.start();
    this.loading = true;
    this.$store
      .dispatch("fetchPeople", { sz_companyid: this.sz_companyid })
      .then(res => {
        const params_arr = [];
        res.data.forEach(el => {
          params_arr.push(el);
        });
        this.tabdata = [...params_arr];
        this.loading = false;
        this.$snotify.info(`${res.data.length} employee were found`);
      })
      .catch(err => {
        this.loading = false;
        this.$snotify.error("Could not get the list of your employees");
        console.log(err);
      });
  }
};
</script>

