<template>
  <div style="background-color: rgba(246, 248, 255, 0.733)">
    <div class="container mb-8">
      <div class="row justify-content-center">
        <div style="width: 800px;">
          <form @submit.prevent="addDependant">
            <fieldset class="scheduler-border shadow-lg">
              <legend class="scheduler-border">
                <h3 class="m-5">ADD DEPENDENTS</h3>
              </legend>
              <div class="container py-3">
                <button type="button" class="btn btn-outline-success btn-sm btn-block m-1">
                  <h2 class="m-0" style="font-size: 15px " @click="addDepend">
                    <i class="fas fa-user-plus"></i>
                    DEPENDENT
                  </h2>
                </button>
                <div class="form-group mt-4" v-for="(dependant, index) in dependants" :key="index">
                  <div class="row">
                    <div class="col-5">
                      <div>
                        <label for>NAME</label>
                        <input
                          v-model="dependant.name"
                          name="child"
                          type="text"
                          class="form-control"
                          required
                        />
                      </div>
                    </div>

                    <div class="col-3">
                      <div>
                        <label for>DATE OF BIRTH</label>
                        <input
                          class="form-control"
                          type="date"
                          id="start"
                          value="2018-07-22"
                          v-model="dependant.dob"
                        />
                      </div>
                    </div>
                    <div class="col-3">
                      <div>
                        <label for>RELATION</label>
                        <input
                          class="form-control"
                          type="text"
                          value="2018-07-22"
                          v-model="dependant.relation"
                        />
                      </div>
                    </div>

                    <span class="col">
                      <i
                        style="color: red"
                        class="fas fa-minus-circle"
                        @click="removeDepend(index)"
                      ></i>
                    </span>
                  </div>
                  <label for>PHONE NUMBER</label>
                  <div class="row">
                    <div class="col-3">
                      <select name="phone-code" id="phone-code" class="form-control">
                        <option value="+233">+233 GH</option>
                        <option value="+234">+234 NGN</option>
                      </select>
                    </div>
                    <div class="col-9">
                      <input
                        class="form-control"
                        type="tel"
                        placeholder
                        v-model="dependant.sz_phoneno"
                      />
                    </div>
                  </div>
                </div>
                <div class="animated fadeIn py-4 text-center">
                  <button
                    type="submit"
                    class="btn btn-primary btn-rounded"
                    style="border-radius:25px;"
                    :disabled="loading"
                  >
                    <i v-if="loading" class="fa fa-spin fa-spinner"></i>
                    <template v-else>ADD EMPLOYEE</template>
                  </button>
                </div>
              </div>
            </fieldset>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import http from "@/network/Base.js";

export default {
  data() {
    return {
      loading: false,
      dependants: []
    };
  },
  methods: {
    addDepend() {
      this.dependants.push({
        name: "",
        relation: "",
        dob: "",
        sz_email: "user@email.com",
        sz_phoneno: ""
      });
    },

    removeDepend(index) {
      this.dependants.splice(index, 1);
    },
    gmSpeciallyStringifyArray(array) {
      if (Array.isArray(array)) {
        let t = JSON.stringify(array);
        return t;
      } else {
        return null;
      }
    },
    addDependant() {
      let payload = {
        adminid: this.iD,
        sz_firstname: this.sz_fname,
        sz_lastname: this.sz_lname,
        sz_othername: this.sz_oname,
        sz_email: this.sz_email,
        sz_gender: this.sz_gender,
        sz_dob: this.sz_dob,
        sz_phoneno: this.sz_phoneno,
        sz_companyname: this.companyName,
        sz_companyid: this.sz_companyid,
        dependents: !!this.gmSpeciallyStringifyArray(this.dependants)
          ? this.gmSpeciallyStringifyArray(this.dependants)
          : []
      };

      this.loading = true;
      this.$store
        .dispatch("addEmployeeAndDependents", payload)
        .then(res => {
          this.$router.push({ path: "/employee" });
          this.loading = false;
          this.$snotify.success(`Employee created successfully`);
        })
        .catch(err => {
          this.loading = false;
          this.$snotify.error("Error during employee account creation");
        });
      //this.$store.commit("employee/setEmployeeDetails", this.company);
    }
  },
  computed: {
    ...mapState({
      companyName: state => state.corporate.company_name,
      sz_companyid: state => state.corporate.company_id,
      iD: state => state.corporate.id,
      sz_fname: state => state.employee.first_name,
      sz_lname: state => state.employee.last_name,
      sz_oname: state => state.employee.other_name,
      sz_gender: state => state.employee.gender,
      sz_email: state => state.employee.work_email,
      sz_phoneno: state => state.employee.phone_number,
      sz_dob: state => state.employee.dob
    })
  }
};
</script>

