<script>
import http from "@/network/Base.js";
// import onBoard  from '@/network/corporate/register.js'
// import { mapState } from 'vuex'

export default {
  data() {
    return {
      loading: false,
      company: {
        sz_companyname: "",
        sz_phoneno: "",
        sz_website: "",
        sz_address: ""
      },
      errInpMessages: {
        companyName: null,
        phoneNumber: null,
        website: null,
        companyAddress: null
      }
    };
  },
  methods: {
    onBoarding() {
      if (
        !this.company.sz_companyname ||
        this.company.sz_companyname.length < 3
      ) {
        this.errInpMessages.companyName =
          "Your name must contain at least 2 characters";
        return;
      }
      if (!this.company.sz_website || this.company.sz_website.length < 3) {
        this.errInpMessages.webSite = "Your website must contain characters";
        return;
      }
      if (!this.company.sz_phoneno) {
        this.errInpMessages.phoneNumber = "Please enter a valid number";
        return;
      }
      if (!this.company.sz_address || this.company.sz_address.length < 3) {
        this.errInpMessages.companyAddress =
          "Please enter a valid phone number";
        return;
      }

      this.loading = true;
      this.$store
        .dispatch("verifyCompanyExistence", this.company)
        .then(() => {
          this.$router.push("/account");
        })
        .catch(err => {
          this.loading = false;
          this.$snotify.error("Sorry this company already exists!");
        });

      this.$store.commit("corporate/setCompanyDetails", this.company);
      this.errInpMessages = {
        companyName: null,
        phoneNumber: null,
        website: null,
        companyAddress: null
      };
    }
  }
};
</script>

